h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #f1f5f4;
  margin: 0;
}

@media (width >= 640px) {
  .lock-scrollbar {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll !important;
  }
}

.container {
  padding-inline: 20px;
}

@media (width >= 640px) {
  .container {
    padding-inline: 39px;
  }
}

@media (width >= 1280px) and (width <= 1440px) {
  .container {
    padding-inline: 55px;
  }
}

p, ul, li, a, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

h1, h2, h3, h4, h5, h6 {
  color: #1c1b29;
  font-family: Playfair Display, sans-serif;
  font-weight: 700;
}

h1 {
