.subscribe {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  display: grid;
}

@media (width >= 640px) {
  .subscribe {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) and (width <= 1440px) {
