  }
}

.subscribe__title {
  grid-area: 1 / 1 / auto / -1;
}

.subscribe__text {
  grid-column: 1 / -1;
  margin: 20px 0 50px;
  font-weight: 400;
}

@media (width >= 640px) {
  .subscribe__text {
    grid-column: 1 / 6;
  }
}

.subscribe__form {
  grid-column: 1 / -1;
