  }
}

.events {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  gap: 60px 20px;
  margin-top: 120px;
  display: grid;
}

@media (width >= 640px) {
  .events {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) and (width <= 1440px) {
  .events {
