  }
}

.news {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  gap: 60px 20px;
  margin: 90px 0 120px;
  display: grid;
}

@media (width >= 640px) {
  .news {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) and (width <= 1440px) {
  .news {
    --columns: 12;
  }
}

.news__title {
  text-align: left;
  grid-area: 1 / 1 / auto / -1;
  margin-bottom: 60px;
}

.card {
  flex-direction: column;
  grid-column: 1 / -1;
  display: flex;
}

.card__column {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  display: flex;
}

@media (width >= 640px) {
  .card--event {
    --columns: 2;
    grid-template-columns: repeat(var(--columns), 1fr);
    column-gap: 20px;
    display: grid;
  }
}

@media (width >= 640px) and (width >= 640px) {
  .card--event {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 640px) and (width >= 1280px) and (width <= 1440px) {
  .card--event {
    --columns: 12;
  }
}

@media (width >= 640px) {
  .card--left {
    grid-column: 1 / 4;
  }
}

@media (width >= 1280px) and (width <= 1440px) {
  .card--left {
    grid-column: 1 / 7;
  }
}

@media (width >= 640px) {
  .card--right {
    grid-column: 4 / 7;
  }
}

@media (width >= 1280px) and (width <= 1440px) {
