}

@media (width >= 640px) {
  .card__date--event {
    grid-area: 1 / 4 / auto / 7;
  }
}

@media (width >= 1280px) and (width <= 1440px) {
  .card__date--event {
    grid-column: 5 / 8;
  }
}

.card__date--news {
