  }
}

@media (width >= 1280px) and (width <= 1440px) {
  .card__text--event {
    grid-column: 5 / 11;
  }

  .card__button {
    width: 270px;
  }
}

@media (width >= 640px) {
  .card__button--event {
    grid-column: 4 / 7;
  }
}

@media (width >= 1280px) and (width <= 1440px) {
  .card__button--event {
    grid-column: 5 / 8;
  }
}

.header {
