  display: grid;
}

@media (width >= 640px) {
  .header__bottom {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) and (width <= 1440px) {
  .header__bottom {
    --columns: 12;
  }
}

@media (width >= 640px) {
