}

.menu__about {
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  display: flex;
}

@media (width >= 640px) {
  .menu__about {
    --columns: 2;
    grid-template-columns: repeat(var(--columns), 1fr);
