.nav {
  margin-block: 40px;
}

@media (width >= 1280px) and (width <= 1440px) {
  .nav {
    margin-block: 70px;
  }
}

.nav__ul {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.nav__link > h5 {
  color: #f1f5f4;
  display: inline-block;
  position: relative;
}

@media (width >= 640px) {
  .nav__link > h5 {
    font-size: 40px;
    line-height: 50px;
  }
}

@media (width >= 1280px) and (width <= 1440px) {
  .nav__link > h5 {
    font-size: 48px;
    line-height: 57.6px;
  }
}

.nav__link > h5:after {
  content: "";
  transform-origin: 0;
  background-color: #f1f5f4;
  width: 100%;
  height: 1px;
  transition: transform .3s;
  display: block;
  position: absolute;
  bottom: -3px;
  transform: scale(0);
}

.nav__link > h5:hover:after {
  transform: scale(1);
}

.page__menu {
  scrollbar-width: none;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow: auto;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

